:root{
    --medium-font--:18px;
    --qustion-font--:19px;
    --option-font--:18px;
}

form{
    display: flex;
    flex-direction: column;
    gap: 10px;
    align-items: end;
}
.btn-parent{
    width: 100%;
    height: 8vh;
    display: flex;
    align-items: center;
    justify-content: center;
}
.question-box{
    width: 100%;
    height: 60%;
    border-bottom: 1px solid;
    padding: 20px;
}
.question{
    width: 100%;
    height: 40%;
    display: flex;
    flex-direction: row-reverse;
    align-items: center;
    justify-content: end;
    gap: 10px;
}
h2{
    font-size: var(--qustion-font--);
}
.options{
    width: 100%;
    display: flex;
    flex-direction: column;
    align-items: end;
    justify-content: center;
    /* gap: 20px; */
    padding-right:20px ;
}

.cyberpunk-checkbox {
    appearance: none;
    width: 20px;
    height: 20px;
    border: 2px solid #30cfd0;
    border-radius: 5px;
    background-color: transparent;
    display: inline-block;
    position: relative;
    margin-right: 10px;
    cursor: pointer;
}

.cyberpunk-checkbox:before {
  content: "";
  background-color: #30cfd0;
  display: block;
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%) scale(0);
  width: 10px;
  height: 10px;
  border-radius: 3px;
  transition: all 0.3s ease-in-out;
}

.cyberpunk-checkbox:checked:before {
  transform: translate(-50%, -50%) scale(1);
}

.cyberpunk-checkbox-label {
  font-size:var(--medium-font--);
  color: #fff;
  cursor: pointer;
  user-select: none;
  display: flex;
  align-items: center;
}

.op1,.op2{
    display: flex;
    gap: 10px;
    justify-content: end;
    align-items: center;
    margin-top: 10px;
    font-size: var(--option-font--);
}
.question-number{
    font-size: var(--medium-font--);
}
.btn{
    cursor: pointer;
    width: 200px;
    height: 50px;
    border-radius: 16px;
    border: 2px solid #30cfd0;
    font-size:var(--medium-font--);
    background: none;
}
.err{
    color: red;
}

@media only screen and (max-width:1413px) {
    :root{
        --medium-font--:18px;
        --qustion-font--:17px;
        --option-font--:10px;
    }
}
@media only screen and (max-width:1217px) {
    :root{
        --medium-font--:18px;
        --qustion-font--:15px;
        --option-font--:16px;
    }
}
@media only screen and (max-width:1091px) {
    :root{
        --medium-font--:18px;
        --qustion-font--:13px;
        --option-font--:13px;
    }
}
@media only screen and (max-width:773px) {
    :root{
        --medium-font--:15px;
        --qustion-font--:11px;
        --option-font--:12px;
    }
    .cyberpunk-checkbox{
        width: 17px;
        height: 17px;
    }
}
@media only screen and (max-width:671px) {
    :root{
        --medium-font--:11px;
        --qustion-font--:1.7vw;
        --option-font--:11px;
    }
    .btn{
        width: 100px;
        height: 40px;
    }
}
@media only screen and (max-width:511px) {
    :root{
        --medium-font--:11px;
        --qustion-font--:9px;
        --option-font--:8px;
    }
    .cyberpunk-checkbox{
        width: 14px;
        height: 14px;
    }
    .cyberpunk-checkbox:before{
        width: 7px;
        height: 7px;
    }
}
@media only screen and (max-width:415px) {
    :root{
        --medium-font--:11px;
        --qustion-font--:8px;
        --option-font--:7px;
    }
}