.question-container{
    width: 90%;
    height: 100%;
    border-radius: 32px;
    background-color: #edf2f0;
    display: flex;
    flex-direction: column;
    gap: 10px;
}
@media only screen and (max-width:889px) {
    .question-container{
        width: 100%;
        border-radius: 0;
    }
}