@font-face {
    font-family: iranYekan;
    src: url(/public/assets/font/Qs_Iranyekan.ttf) format(ttf),
    url(/public/assets/font/Qs_Iranyekan.eot) format(eot),
    url(/public/assets/font/Qs_Iranyekan.woff2) format(woff2),
    url(/public/assets/font/Qs_Iranyekan.woff) format(woff);
}
*{
    padding: 0;
    margin: 0;
    box-sizing: border-box;
    font-family: iranYekan;
}
body::-webkit-scrollbar{
    background-color: #fafffd;
}
body::-webkit-scrollbar-thumb{
    background-color: #004fa3;
    border-radius: 5px;
}
.main-parent{
    /* overflow-x: hidden; */
    width: 100%;
    height: 100%;
    background: rgb(0,197,203);
    background: linear-gradient(180deg, rgba(0,197,203,1) 20%, rgba(0,122,255,1) 100%);
    display: flex;
    justify-content: center;
    align-items: center;
    padding: 20px;
}
@media only screen and (max-width:773px) {
    .main-parent{
        padding: 0;
    }
}